'use client';

import { useState } from 'react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Textarea } from '@/components/ui/textarea';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger } from '@/components/ui/dialog';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { useCRMStore } from '@/hooks/useCRMStore';
import { Opportunity, OpportunityStatus, OpportunityStatusLabels } from '@/lib/crmTypes';

interface OpportunityFormProps {
  opportunity?: Opportunity;
  onClose?: () => void;
  trigger?: React.ReactNode;
  defaultStatus?: OpportunityStatus;
}

export function OpportunityForm({ opportunity, onClose, trigger, defaultStatus }: OpportunityFormProps) {
  const { addOpportunity, updateOpportunity, state } = useCRMStore();
  const [open, setOpen] = useState(false);
  const [formData, setFormData] = useState({
    title: opportunity?.title || '',
    value: opportunity?.value || 0,
    status: opportunity?.status || defaultStatus || OpportunityStatus.NUEVO,
    estimatedClosingDate: opportunity?.estimatedClosingDate 
      ? opportunity.estimatedClosingDate.toISOString().split('T')[0]
      : '',
    contactId: opportunity?.contactId || '',
    description: opportunity?.description || ''
  });
  const [errors, setErrors] = useState<Record<string, string>>({});

  const validateForm = () => {
    const newErrors: Record<string, string> = {};
    
    if (!formData.title.trim()) {
      newErrors.title = 'El t√≠tulo es obligatorio';
    }
    
    if (formData.value <= 0) {
      newErrors.value = 'El valor debe ser mayor a 0';
    }
    
    if (!formData.estimatedClosingDate) {
      newErrors.estimatedClosingDate = 'La fecha estimada de cierre es obligatoria';
    }
    
    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!validateForm()) {
      return;
    }

    const opportunityData = {
      ...formData,
      value: Number(formData.value),
      estimatedClosingDate: new Date(formData.estimatedClosingDate),
      contactId: formData.contactId || undefined
    };

    if (opportunity) {
      updateOpportunity({
        ...opportunity,
        ...opportunityData,
        updatedAt: new Date()
      });
    } else {
      addOpportunity(opportunityData);
    }

    setFormData({
      title: '',
      value: 0,
      status: OpportunityStatus.NUEVO,
      estimatedClosingDate: '',
      contactId: '',
      description: ''
    });
    setOpen(false);
    onClose?.();
  };

  const formContent = (
    <form onSubmit={handleSubmit} className="space-y-4">
      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
        <div className="space-y-2">
          <Label htmlFor="title">T√≠tulo *</Label>
          <Input
            id="title"
            value={formData.title}
            onChange={(e) => setFormData(prev => ({ ...prev, title: e.target.value }))}
            className={errors.title ? 'border-red-500' : ''}
            placeholder="Ej: Venta de software CRM"
          />
          {errors.title && <p className="text-sm text-red-500">{errors.title}</p>}
        </div>
        
        <div className="space-y-2">
          <Label htmlFor="value">Valor (‚Ç¨) *</Label>
          <Input
            id="value"
            type="number"
            min="0"
            step="0.01"
            value={formData.value}
            onChange={(e) => setFormData(prev => ({ ...prev, value: parseFloat(e.target.value) || 0 }))}
            className={errors.value ? 'border-red-500' : ''}
            placeholder="0.00"
          />
          {errors.value && <p className="text-sm text-red-500">{errors.value}</p>}
        </div>
        
        <div className="space-y-2">
          <Label htmlFor="status">Estado</Label>
          <Select 
            value={formData.status} 
            onValueChange={(value: OpportunityStatus) => setFormData(prev => ({ ...prev, status: value }))}
          >
            <SelectTrigger>
              <SelectValue />
            </SelectTrigger>
            <SelectContent>
              {Object.values(OpportunityStatus).map(status => (
                <SelectItem key={status} value={status}>
                  {OpportunityStatusLabels[status]}
                </SelectItem>
              ))}
            </SelectContent>
          </Select>
        </div>
        
        <div className="space-y-2">
          <Label htmlFor="estimatedClosingDate">Fecha Estimada de Cierre *</Label>
          <Input
            id="estimatedClosingDate"
            type="date"
            value={formData.estimatedClosingDate}
            onChange={(e) => setFormData(prev => ({ ...prev, estimatedClosingDate: e.target.value }))}
            className={errors.estimatedClosingDate ? 'border-red-500' : ''}
          />
          {errors.estimatedClosingDate && <p className="text-sm text-red-500">{errors.estimatedClosingDate}</p>}
        </div>
      </div>
      
      <div className="space-y-2">
        <Label htmlFor="contactId">Contacto Asociado</Label>
        <Select 
          value={formData.contactId} 
          onValueChange={(value) => setFormData(prev => ({ ...prev, contactId: value }))}
        >
          <SelectTrigger>
            <SelectValue placeholder="Seleccionar contacto (opcional)" />
          </SelectTrigger>
          <SelectContent>
            <SelectItem value="">Sin contacto asociado</SelectItem>
            {state.contacts.map(contact => (
              <SelectItem key={contact.id} value={contact.id}>
                {contact.name} - {contact.company}
              </SelectItem>
            ))}
          </SelectContent>
        </Select>
      </div>
      
      <div className="space-y-2">
        <Label htmlFor="description">Descripci√≥n</Label>
        <Textarea
          id="description"
          value={formData.description}
          onChange={(e) => setFormData(prev => ({ ...prev, description: e.target.value }))}
          rows={3}
          placeholder="Detalles adicionales sobre la oportunidad..."
        />
      </div>
      
      <div className="flex justify-end space-x-2 pt-4">
        <Button type="button" variant="outline" onClick={() => setOpen(false)}>
          Cancelar
        </Button>
        <Button type="submit">
          {opportunity ? 'Actualizar' : 'Crear'} Oportunidad
        </Button>
      </div>
    </form>
  );

  if (trigger) {
    return (
      <Dialog open={open} onOpenChange={setOpen}>
        <DialogTrigger asChild>
          {trigger}
        </DialogTrigger>
        <DialogContent className="max-w-2xl">
          <DialogHeader>
            <DialogTitle>
              {opportunity ? 'Editar Oportunidad' : 'Nueva Oportunidad'}
            </DialogTitle>
          </DialogHeader>
          {formContent}
        </DialogContent>
      </Dialog>
    );
  }

  return (
    <Card>
      <CardHeader>
        <CardTitle>{opportunity ? 'Editar Oportunidad' : 'Nueva Oportunidad'}</CardTitle>
      </CardHeader>
      <CardContent>
        {formContent}
      </CardContent>
    </Card>
  );
}
