'use client';

import { useState } from 'react';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { AlertDialog, AlertDialogAction, AlertDialogCancel, AlertDialogContent, AlertDialogDescription, AlertDialogFooter, AlertDialogHeader, AlertDialogTitle, AlertDialogTrigger } from '@/components/ui/alert-dialog';
import { useCRMStore } from '@/hooks/useCRMStore';
import { Opportunity, OpportunityStatus, OpportunityStatusLabels, formatCurrency, formatDate } from '@/lib/crmTypes';
import { OpportunityForm } from './OpportunityForm';

export function OpportunityPipeline() {
  const { state, deleteOpportunity, updateOpportunity } = useCRMStore();

  const getOpportunitiesByStatus = (status: OpportunityStatus): Opportunity[] => {
    return state.opportunities.filter(opportunity => opportunity.status === status);
  };

  const getTotalValueByStatus = (status: OpportunityStatus): number => {
    return getOpportunitiesByStatus(status).reduce((total, opportunity) => total + opportunity.value, 0);
  };

  const getContactName = (contactId?: string): string => {
    if (!contactId) return 'Sin contacto';
    const contact = state.contacts.find(c => c.id === contactId);
    return contact ? contact.name : 'Contacto no encontrado';
  };

  const handleDeleteOpportunity = (opportunityId: string) => {
    deleteOpportunity(opportunityId);
  };

  const moveOpportunity = (opportunity: Opportunity, newStatus: OpportunityStatus) => {
    updateOpportunity({
      ...opportunity,
      status: newStatus,
      updatedAt: new Date()
    });
  };

  const OpportunityCard = ({ opportunity }: { opportunity: Opportunity }) => {
    const isOverdue = new Date(opportunity.estimatedClosingDate) < new Date() && 
                     opportunity.status !== OpportunityStatus.GANADO && 
                     opportunity.status !== OpportunityStatus.PERDIDO;

    return (
      <Card className={`mb-3 hover:shadow-md transition-shadow ${isOverdue ? 'border-red-200 bg-red-50' : ''}`}>
        <CardContent className="p-4">
          <div className="flex justify-between items-start mb-2">
            <h4 className="font-semibold text-sm text-slate-900 line-clamp-2">
              {opportunity.title}
            </h4>
            <div className="flex space-x-1 ml-2">
              <OpportunityForm
                opportunity={opportunity}
                trigger={
                  <Button variant="ghost" size="sm" className="h-6 w-6 p-0">
                    ‚úèÔ∏è
                  </Button>
                }
              />
              <AlertDialog>
                <AlertDialogTrigger asChild>
                  <Button variant="ghost" size="sm" className="h-6 w-6 p-0 text-red-600 hover:text-red-700">
                    üóëÔ∏è
                  </Button>
                </AlertDialogTrigger>
                <AlertDialogContent>
                  <AlertDialogHeader>
                    <AlertDialogTitle>¬øEliminar oportunidad?</AlertDialogTitle>
                    <AlertDialogDescription>
                      Esta acci√≥n no se puede deshacer. Se eliminar√° permanentemente la oportunidad "{opportunity.title}".
                    </AlertDialogDescription>
                  </AlertDialogHeader>
                  <AlertDialogFooter>
                    <AlertDialogCancel>Cancelar</AlertDialogCancel>
                    <AlertDialogAction
                      onClick={() => handleDeleteOpportunity(opportunity.id)}
                      className="bg-red-600 hover:bg-red-700"
                    >
                      Eliminar
                    </AlertDialogAction>
                  </AlertDialogFooter>
                </AlertDialogContent>
              </AlertDialog>
            </div>
          </div>
          
          <div className="space-y-2 text-xs">
            <div className="font-semibold text-green-600">
              {formatCurrency(opportunity.value)}
            </div>
            
            <div className="text-slate-600">
              {getContactName(opportunity.contactId)}
            </div>
            
            <div className={`text-slate-600 ${isOverdue ? 'text-red-600 font-medium' : ''}`}>
              Cierre: {formatDate(opportunity.estimatedClosingDate)}
              {isOverdue && <span className="ml-1">‚ö†Ô∏è</span>}
            </div>
            
            {opportunity.description && (
              <div className="text-slate-500 line-clamp-2">
                {opportunity.description}
              </div>
            )}
          </div>
          
          {/* Quick Status Change Buttons */}
          <div className="mt-3 flex flex-wrap gap-1">
            {Object.values(OpportunityStatus).filter(status => status !== opportunity.status).map(status => (
              <Button
                key={status}
                variant="outline"
                size="sm"
                className="text-xs h-6 px-2"
                onClick={() => moveOpportunity(opportunity, status)}
              >
                ‚Üí {OpportunityStatusLabels[status]}
              </Button>
            ))}
          </div>
        </CardContent>
      </Card>
    );
  };

  const PipelineColumn = ({ status }: { status: OpportunityStatus }) => {
    const opportunities = getOpportunitiesByStatus(status);
    const totalValue = getTotalValueByStatus(status);
    
    const getColumnColor = (status: OpportunityStatus) => {
      switch (status) {
        case OpportunityStatus.NUEVO:
          return 'border-blue-200 bg-blue-50';
        case OpportunityStatus.EN_PROGRESO:
          return 'border-yellow-200 bg-yellow-50';
        case OpportunityStatus.GANADO:
          return 'border-green-200 bg-green-50';
        case OpportunityStatus.PERDIDO:
          return 'border-red-200 bg-red-50';
        default:
          return 'border-slate-200 bg-slate-50';
      }
    };

    return (
      <div className={`rounded-lg border-2 ${getColumnColor(status)} p-4 min-h-[500px]`}>
        <div className="flex justify-between items-center mb-4">
          <div>
            <h3 className="font-semibold text-slate-900">
              {OpportunityStatusLabels[status]}
            </h3>
            <p className="text-sm text-slate-600">
              {opportunities.length} oportunidad{opportunities.length !== 1 ? 'es' : ''}
            </p>
          </div>
          <div className="text-right">
            <div className="font-semibold text-green-600">
              {formatCurrency(totalValue)}
            </div>
            <OpportunityForm
              defaultStatus={status}
              trigger={
                <Button variant="outline" size="sm" className="mt-1">
                  + Agregar
                </Button>
              }
            />
          </div>
        </div>
        
        <div className="space-y-2">
          {opportunities.map(opportunity => (
            <OpportunityCard key={opportunity.id} opportunity={opportunity} />
          ))}
          
          {opportunities.length === 0 && (
            <div className="text-center py-8 text-slate-500">
              <p className="mb-2">No hay oportunidades</p>
              <OpportunityForm
                defaultStatus={status}
                trigger={
                  <Button variant="ghost" size="sm">
                    Agregar primera oportunidad
                  </Button>
                }
              />
            </div>
          )}
        </div>
      </div>
    );
  };

  const totalOpportunities = state.opportunities.length;
  const totalValue = state.opportunities.reduce((total, opportunity) => total + opportunity.value, 0);
  const wonValue = getTotalValueByStatus(OpportunityStatus.GANADO);
  const conversionRate = totalOpportunities > 0 ? (getOpportunitiesByStatus(OpportunityStatus.GANADO).length / totalOpportunities * 100) : 0;

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
        <div>
          <h1 className="text-3xl font-bold text-slate-900">Pipeline de Oportunidades</h1>
          <p className="text-slate-600">Gestiona tus oportunidades de venta</p>
        </div>
        <OpportunityForm
          trigger={
            <Button size="lg">
              Nueva Oportunidad
            </Button>
          }
        />
      </div>

      {/* Summary Stats */}
      <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
        <Card>
          <CardContent className="p-4">
            <div className="text-2xl font-bold text-slate-900">{totalOpportunities}</div>
            <div className="text-sm text-slate-600">Total Oportunidades</div>
          </CardContent>
        </Card>
        <Card>
          <CardContent className="p-4">
            <div className="text-2xl font-bold text-green-600">{formatCurrency(totalValue)}</div>
            <div className="text-sm text-slate-600">Valor Total</div>
          </CardContent>
        </Card>
        <Card>
          <CardContent className="p-4">
            <div className="text-2xl font-bold text-blue-600">{formatCurrency(wonValue)}</div>
            <div className="text-sm text-slate-600">Valor Ganado</div>
          </CardContent>
        </Card>
        <Card>
          <CardContent className="p-4">
            <div className="text-2xl font-bold text-purple-600">{conversionRate.toFixed(1)}%</div>
            <div className="text-sm text-slate-600">Tasa de Conversi√≥n</div>
          </CardContent>
        </Card>
      </div>

      {/* Pipeline Board */}
      <div className="grid grid-cols-1 lg:grid-cols-4 gap-4">
        {Object.values(OpportunityStatus).map(status => (
          <PipelineColumn key={status} status={status} />
        ))}
      </div>

      {/* Empty State */}
      {totalOpportunities === 0 && (
        <Card>
          <CardContent className="p-8 text-center">
            <h3 className="text-lg font-semibold text-slate-900 mb-2">
              No hay oportunidades
            </h3>
            <p className="text-slate-600 mb-4">
              Comienza agregando tu primera oportunidad de venta
            </p>
            <OpportunityForm
              trigger={
                <Button>
                  Crear Primera Oportunidad
                </Button>
              }
            />
          </CardContent>
        </Card>
      )}
    </div>
  );
}
